/* Main container */
.engservicefirstpage-about-container {
  display: flex;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;
  box-sizing: border-box; /* Ensure padding and borders don't overflow */
  min-height: calc(100vh - nav-height); /* Ensure it doesn't exceed viewport */
  margin-top: 10px;
}

/* Image section */
.engservicefirstpage-image-section {
  width: 60%;
  padding-top:60px;
  position: relative;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
}

.engservicefirstpage-about-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Text section */
.engservicefirstpage-text-section {
  width: 85%;
  background-color: #387EC8;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  clip-path: polygon(10% 0, 100% 0, 100% 100%, 0% 100%);
  margin-left: -6%;
}

/* Diagonal shaded line */
.engservicefirstpage-about-container::before {
  content: "";
  position: absolute;
  z-index: 2;
  background: linear-gradient(to bottom right, #387EC8, rgba(214, 215, 219, 0));
  pointer-events: none;
  left: 39%; /* Lean closer to the text section */
  width: 3.5%; /* Thicker line */
  height: 180%; /* Extends the line significantly */
  top: -25%;
  transform: skewX(-8deg) translate(-5px, 10px); /* Fine horizontal and vertical tuning */
}

.engservicefirstpage-text-section h1 {
  font-size: 3em;
  margin-bottom: 10px;
  z-index: 3;
  font-family: Arial, Helvetica, sans-serif;
}

.engservicefirstpage-text-section p {
  font-size: 1.5em;
  text-align: center;
  padding: 0 20px;
  z-index: 3;
}

.container {
  display: flex;
  flex-direction: row;
  padding: 20px;
  gap: 20px;
}

.image-wrapper {
  flex: 1;
}

.image {
  width: 100%;
  max-width: 400px;
  border-radius: 8px;
  
}

.text-wrapper {
  flex: 2;
  padding: 10px;
}

.info-section {
  margin: 20px 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .engservicefirstpage-about-container {
    flex-direction: column; /* Stack sections vertically */
    height: auto; /* Adjust height for stacked layout */
  }

  .engservicefirstpage-image-section {
    width: 120%; /* Full width for the image section */
    height: 55vh; /* Half the viewport height */
    clip-path: none; /* Remove clip-path for a full rectangular display */
    
  }

  .engservicefirstpage-text-section {
    width: 100%; /* Full width for the text section */
    height: 25vh;
    margin-left: 0; /* Remove overlap */
    clip-path: none; /* Remove clip-path for a rectangular display */
    padding: 20px; /* Add padding for spacing */
  }

  .engservicefirstpage-text-section h1 {
    font-size: 2em; /* Adjust font size for smaller screens */
    margin-right: 40px;
  }

  .engservicefirstpage-text-section p {
    font-size: 1em; /* Adjust paragraph font size */
    margin-right: 15px;
    text-align: left;
  }

  .engservicefirstpage-about-container::before {
    display: none; /* Hide the diagonal line in mobile view */
  }
}
