/* Main container */
.three-d-modeling-container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 120px;
    background-color: #f5f5f5;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    gap: 20px;
}

/* Left Section - Image */
.three-d-modeling-image {
    position: relative;
    width: 35%;
    order: 1;
    padding: 10px 0;
    margin-top: 0;
}



/* Right Section - Content */
.three-d-modeling-text {
    width: 60%;
    order: 2;
    margin-top: -75px;

}

.three-d-modeling-text h1 {
    font-size: 2em;
    color: #000;
    margin-bottom: 10px;
}

.three-d-modeling-text h2 {
    font-size: 1.5em;
    color: #555;
    margin-top: 20px;
    margin-bottom: 10px;
}

.three-d-modeling-text p {
    font-size: 1em;
    color: #555;
    margin-bottom: 20px;
}

.activities-list,
.services-list {
    list-style: none;
    padding: 0;
    margin: 0 0 20px 0;
    line-height: 1.8;
}

.activities-list li,
.services-list li {
    font-size: 0.95em;
    color: #333;
    margin-bottom: 10px;
}

/* Button */
.three-d-modeling-button {
    background-color: #00c853;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 1em;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.three-d-modeling-button:hover {
    background-color: #009624;
}

/* Responsive Design */
@media (max-width: 768px) {
    .three-d-modeling-container {
        flex-direction: column;
        align-items: center;
        text-align: left;
        gap: 20px;
    }

    .three-d-modeling-text {
        width: 220%;
        
        margin-left:10px;
    }

    .three-d-modeling-image {
        margin-top:20px;
        width: 220%;
    }
}


/* Media Query for 1366x768 screen size */
@media screen and (min-width: 1390px) and (max-width: 1919px) {
    .three-d-modeling-container {
        padding: 140px;
        gap: 15px;
    }

    .three-d-modeling-text h1 {
        font-size: 1.8em;
    }

    .three-d-modeling-text h2 {
        font-size: 1.4em;
    }

    .three-d-modeling-text p {
        font-size: 0.95em;
    }

    .three-d-modeling-image {
        width: 35%;
    }

    .three-d-modeling-button {
        padding: 8px 18px;
        font-size: 0.9em;
    }
}

/* Media Query for 1920x1080 screen size */
@media screen and (min-width: 1920px) {
    .three-d-modeling-container {
        padding: 60px;
        gap: 25px;
    }

    .three-d-modeling-text h1 {
        font-size: 2.2em;
    }

    .three-d-modeling-text h2 {
        font-size: 1.6em;
    }

    .three-d-modeling-text p {
        font-size: 1.1em;
    }

    .three-d-modeling-image {
        width: 30%;
    }

    .three-d-modeling-button {
        padding: 12px 24px;
        font-size: 1.1em;
    }
}
