.reachus-container {
  margin: 0;
  padding-top:30px;
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
  position: relative;
  min-height: 100vh; /* Full screen height */
  display: flex;
  justify-content: flex-start; /* Align form to the left */
  align-items: center;
  overflow: hidden; /* Prevent overflow */
  margin-top:50px;
}

.content-section {
  position: relative;
  width: 100%;
  height: 100vh; /* Full screen height */
  overflow: hidden;
}

.map-box {
  position: absolute; /* Allow the map to serve as the background */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; /* Cover entire container */
  z-index: 0; /* Ensure it's behind the form */
  pointer-events: auto; /* Prevent map interaction */
}

.map-box iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.form-box {
  margin-top:55px;
  position: relative;
  z-index: 2; /* Ensure it's above the map */
  background: rgba(255, 255, 255, 0.9); /* Semi-transparent background */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  width: 350px;
  max-width: 90%;
  margin-left: 70%; /* Align form to the left */
  text-align: left;
}

.contact-form label {
  margin-top: 10px;
  font-size: 0.85em; 
  font-weight: bold;
  color: #333;
}

.input-field,
.textarea-field {
  margin-top: 5px;
  width: 95%; 
  padding: 10px; 
  font-size: 0.9em; 
  border: 1px solid #ccc;
  border-radius: 5px;
}

.textarea-field {
  resize: none;
}

.send-button {
  width: 102%; 
  height: 40px;
  font-size: 1em; 
  background-color: #28a745;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.send-button:hover {
  background-color: #218838;
}


@media screen and (min-width: 1920px) {
.reachus-container{
  padding-top:50px;
}

}
