/* Mobile view */
@media (max-width: 768px) {
  .reachus-container {
    flex-direction: column; /* Stack the form and map vertically */
    align-items: center; /* Center the content */
    margin-top: 0px;
  }

  .map-box {
    pointer-events: auto;
    margin-top: 20px; /* Reduced margin for mobile */
    height: 200px; /* Reduce map height for mobile view */
    width: 100%; /* Adjust map width */
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); /* Card effect for map */
    border-radius: 10px; /* Rounded corners */
    margin-bottom: 20px; /* Add some space below the map */
  }

  .form-box {
    width: 80%; /* Adjust width for mobile */
    margin-top: -50px; /* Add margin top */
  }

  .form-box, .map-box {
    border-radius: 10px; /* Apply rounded corners for cards */
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); /* Apply card shadow effect */
  }

  .form-box {
    margin-top: 240px; /* Adjust top margin for the form */
    margin-left:20px;
  }
